<template>
  <div class="address_a">
    <header class="count_top header">
      <img src="../../../assets/shopImage/left_icon.png" alt="" @click="back">
      <span>银行卡</span>
    </header>
    <div class="site-box">
      <div class="site zoomBor1"  v-for="(item, index) in data">
        <div class="site-d1" @click="select(item)">
          <div class="htwo lineOne">{{item.bank_name}}</div>
          <div class="textyin lineOne">尾号{{item.bank_number | bankFilter}} 储蓄卡</div>
        </div>
        <div class="site-check" @click="chooseSite(item)">
          <img src="../../../assets/person/d_select.png" alt="" v-if="!item.is_true">
          <img src="../../../assets/person/select.png" alt="" v-if="item.is_true">
        </div>
      </div>
    </div>
    <footer class="footer_btn" @click="addAddress">
      新增银行卡
    </footer>
  </div>
</template>

<script>
  import { Toast} from 'mint-ui';
  export default {
    data () {
      return {
        data:[]
      }
    },
    filters:{
      bankFilter(val){
        return val.substr(val.length-4);
      }
    },
    methods: {
      select(item){
        this.post('home/Withdraw/setBankState', {
          id: item.id
        }).then(res => {
          if (res.data.code === 10000) {
            this.$router.push('/withdraw')
          }
        })
      },
      editor(item){
        this.$router.push(`/bindCard/${item.id}`);
      },
      deleteAddress(item, index){
        this.post('home/Withdraw/delBankInfo', {
          id: item.id
        }).then(res => {
          if (res.data.code === 10000) {
            this.data.splice(index, 1);
          } else
            Toast({
              message: res.data.info,
              duration: 1000
            });
        });
      },
      addAddress(){
        // 新增银行卡信息 -1
        this.$router.push('/bindCard/-1');
      },
      back(){
        this.$router.go(-1);
      },
      chooseSite(item) {
        this.data.forEach(item => {
          item.is_true = 0;
        });
        this.post('home/Withdraw/setBankState', {
          id: item.id
        }).then(res => {
          if (res.data.code === 10000) {
            item.is_true = !item.is_true;
          }
        })
      },

      goRout:function (path,item) {

      },
      //获取收货地址
      getAddress(){
        this.post('home/Withdraw/getBankInfo', {}).then(res => {
          if (res.data.code === 10000) {
            this.data = res.data.info;
          }
        })
      }
    },
    mounted(){
      this.getAddress();
    },
  }
</script>

<style scoped>
  @import "../../../../static/header.css";
  .address_a {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .site{
    padding:0 .2rem;
    box-sizing: border-box;
    height:1.5rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #FFFFFF;
  }
  .site:after{
    width:calc(100% - .3rem);
    right:0;
  }
  .site-check{
    width:10%;
  }
  .site-check img {
    width: 0.39rem;
  }
  .site-d1{
    flex:0 0 70%;
    margin-left: 0.2rem;
  }
  .site-d1>div:last-child{
    margin-top:.15rem;
  }
  .site-bian{

  }
  .site-bian img{
    width:.35rem;
    height:.35rem;
  }
  .site-bian img:first-child {
    margin-right: 0.1rem;
  }
  .footer_btn {
    height: 0.88rem;
    background-color: #ff6f00;
    text-align: center;
    line-height: 0.88rem;
    font-size: 0.36rem;
    color: #FFFFFF;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
  }
</style>



